import React from 'react';
import { Link } from 'gatsby';

const PortfolioItem = ({
    imgSrc,
    title,
    description,
    tags,
    link,
}) => (
    <div style={{
        width: 750, height: 300, borderRadius: 15, display: 'flex', marginBottom: 24, overflow: 'hidden',
        boxShadow: '0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23)'
    }}>
        <img src={imgSrc} style={{ width: '60%', height: 'auto' }} />
        <div style={{ padding: 24 }}>
            <div style={{ fontSize: 14, color: '#aaa', marginBottom: 12 }} >
                {tags.map((tag, index) => <span>{tag + (index === tags.length - 1 ? '' : ' / ')}</span>)}
            </div>
            <h4 style={{ fontSize: 32, marginBottom: 12, marginTop: 0 }}>{title}</h4>
            <div style={{ marginBottom: 12 }}>{description}</div>
            <Link to={link}>
                <div style={{
                    cursor: 'pointer', background: '#333', color: 'white',
                    width: 175, height: 50, display: 'flex', justifyContent: 'center', alignItems: 'center',
                    borderRadius: 30, fontWeight: 'bold'
                }}>
                    View Case Study
                </div>
            </Link>
        </div>
    </div>
);

export default PortfolioItem;